body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #f12711;
  background: linear-gradient(to right, #f5af19, #f12711);
}

html,
input,
button {
  font-family: 'Montserrat', sans-serif;
}
.wrapper {
  text-align: center;
  background-color: #282C34;
  color: white;
  display: flex;
  width: 80%;
  min-height: 80vh;
  margin: 10vh auto;
  box-shadow: 0 0 15px black;
  max-width: 1200px;
}
.form {
    text-align: center;
}

.input {
    border: none;
    background-color: transparent;
    border-bottom: 2px solid rgba(255, 69, 0, 0.4);
    color: white;
    margin: 20px 0;
    font-size: 23px;
    width: 80%;
    padding: 5px;
}

.btn {
    text-transform: uppercase;
    border: none;
    padding: 10px 15px;
    font-size: 18px;
    background-color: rgba(255, 69, 0, 0.9);
    border: 2px solid rgba(255, 69, 0, 0.9);
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: .2s;
}

@media(min-width: 800px) {
    .form {
        text-align: left;
    }

    .input {
        width: 45%;
        margin: 60px 20px;
    }

    .btn {
        font-size: 12px;
    }
}

@media(min-width: 1024px) {
    .btn:hover {
        background-color: transparent;
    }
}
.result {
    padding-top: 10px;
    margin: 0 20px;
    text-align: left;
}

.result p:after {
    content: '';
    display: block;
    width: 80%;
    height: 2px;
    background-color: rgba(255, 69, 0, 0.2);
    margin: 15px 0;

}
.backgroundWrapper {
    background: url(/react-weather/static/media/bg.09bc7c58.jpg) no-repeat center;
    background-position: center;
    background-size: cover;
    min-height: 80vh;
    flex-basis: 40%;
    display: none;
    position: relative
}

@media(min-width:600px) {
    .backgroundWrapper {
        background: url(/react-weather/static/media/bg.09bc7c58.jpg) no-repeat center;
        background-position: center;
        background-size: cover;
        min-height: 80vh;
        flex-basis: 50%;
        display: block;
    }

    .backgroundWrapper__title,
    .backgroundWrapper__description {
        position: absolute;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        width: 100%;
    }

    .backgroundWrapper__title {
        top: 35%;
        font-size: 38px;
    }

    .backgroundWrapper__description {
        top: 55%;
        font-size: 14px;
    }
}

@media(min-width: 700px) {
    .backgroundWrapper__title {
        font-size: 45px;
    }

    .backgroundWrapper__description {
        top: 47%;
        font-size: 16px;
    }
}

@media (min-width: 1024px) {
    .backgroundWrapper__title {
        font-size: 55px;
    }

    .backgroundWrapper__description {
        top: 47%;
        font-size: 22px;
    }


}
